import { DATE_CHANGE } from '../routes/Home/modules/home.js';

var today = new Date();
var tomorrow = new Date();
var dayAfter = new Date();
tomorrow.setDate(today.getDate()+1);
dayAfter.setDate(tomorrow.getDate()+1);
const initialState = { startDate: tomorrow, endDate: dayAfter }
export default function datesReducer(state = initialState, action) {
    return action.type === DATE_CHANGE  ? action.data : state;
}



