
export default (store) => ({
    path: 'user-buyDropletPayment',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            const history = require('./components/UserBuyDropletPayment').default
            cb(null, history)

        }, 'user-buyDropletPayment')
    }
})



