
export default (store) => ({
    path: 'user-buyDroplet',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            const history = require('./components/UserBuyDroplet').default
            cb(null, history)

        }, 'user-buyDroplet')
    }
})



