
import axios from 'axios';
import Config from 'Config';
import { LOGIN_SUCCESS, LOGIN_FAILURE } from '../../Login/modules/login';
import { userLogin } from '../../Login/modules/login';
export const SIGNUP_STEP1 = 'SIGNUP_STEP1';
export const SIGNUP_REQUEST = 'SIGNUP_REQUEST';
export const SIGNUP_SUCCESS = 'SIGNUP_SUCCESS';
export const SIGNUP_FAILURE = 'SIGNUP_FAILURE';

export function userSignup(user,callback) {
  const url = `${Config.API_URL}/users`;
  const {email,password} = user;
  console.log(user);
  return (dispatch) => {
      axios.post(url,user)
      .then((res) => {
        callback();
        const d = new Date();
        res.data.AuthenticationResult.expiry = d.setTime((new Date().getTime() + (60*50*1000)));
        return dispatch({
            type: SIGNUP_SUCCESS,
            user:res.data.AuthenticationResult,
            err:null
        })
      })
      .catch((err) => {
        return dispatch({
            type: SIGNUP_FAILURE,
            err: err.response.data
        })
      })

  }
}

export function userSignupStep1(values,callback) {
  callback();
  return (dispatch) => {
    return dispatch({
      type: SIGNUP_STEP1,
      currentUser:values
    })
  }
}



