import { LOGIN_SUCCESS,LOGIN_FAILURE,LOGOUT_SUCCESS, LOGOUT_FAILURE, USER_UPDATE_SUCCESS, USER_UPDATE_FAILURE, CLEAR_AUTH_ERROR } from '../routes/Login/modules/login.js';
import { SIGNUP_SUCCESS, SIGNUP_REQUEST,SIGNUP_FAILURE } from '../routes/SignUp/modules/signup.js';



let user;
try {
    user = JSON.parse(localStorage.getItem('user'));
} catch(e) {
    user = null; // error in the above string (in this case, yes)!
}

const initialState = user ? { loggedIn: true, user } : {loggedIn: false, loggingIn:false, user:null, err:null};
export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case SIGNUP_REQUEST:
      return {
        loggingIn: true,
        loggedIn: false,
        user: action.user,
        err:null
      };
    case SIGNUP_SUCCESS:
      return {
        loggingIn: false,
        loggedIn: true,
        user: action.user,
        err:null
      };
    case SIGNUP_FAILURE:
      return {
        loggingIn:false,
        loggedIn:false,
        user:null,
        err:action.err
      };
    case LOGIN_SUCCESS:
      return {
        loggingIn: false,
        loggedIn: true,
        user: action.user,
        err:null
      };
    case LOGIN_FAILURE:
    return {
      loggingIn: false,
      loggedIn: false,
      err: action.err,
      user: null
    };
    case LOGOUT_SUCCESS:
      return {
        loggedIn: false,
        loggingIn: false,
        user: null,
        err:null
      };
    case LOGOUT_FAILURE:
      return {
        loggingIn: false,
        loggedIn: false,
        user: null,
        err:action.err
      };
    case USER_UPDATE_SUCCESS:
      return{
        loggedIn: state.loggedIn,
        loggingIn: state.loggingIn,
        err: state.err,
        user:{...state.user,...action.user}
      };
    case USER_UPDATE_FAILURE:
      return{
        loggedIn: state.loggedIn,
        loggingIn: state.loggingIn,
        err: state.err,
        user:state.user
      };
    case CLEAR_AUTH_ERROR:
      return {...state,['err']:null};
    default:
      return state;
  }
}



