
export default (store) => ({
    path: 'forgot-password',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            const login = require('./components/ForgotPasswordContainer').default
            cb(null, login)

        }, 'forgotPassword')
    }
})



