import axios from 'axios';
import Config from 'Config';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_FAILURE = 'LOGIN_FAILURE';
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS';
export const LOGOUT_FAILURE = 'LOGOUT_FAILURE';
export const USER_UPDATE_SUCCESS = 'USER_UPDATE_SUCCESS';
export const USER_UPDATE_FAILURE = 'USER_UPDATE_FAILURE';
export const REFRESH_SUCCESS = 'REFRESH_SUCCESS';
export const REFRESH_FAILURE = 'REFRESH_FAILURE';
export const CLEAR_AUTH_ERROR = 'CLEAR_AUTH_ERROR';

export const axiosInstance = axios.create();
axiosInstance.interceptors.request.use((config) => {
    const user = JSON.parse(localStorage.getItem('user'));
    const now = (new Date()).getTime();
    if(now > user.expiry){
      return axios.post(`${Config.API_URL}auth/token`,{refreshToken : user.RefreshToken})
      .then((res) => {
        const d = new Date();
        res.data.AuthenticationResult.expiry = d.setTime((new Date().getTime() + (60*50*1000)));
        localStorage.setItem('user',JSON.stringify(res.data.AuthenticationResult));
        config.headers.Authorization = `Bearer ${res.data.AuthenticationResult.AccessToken}`;
        console.log('config before return', config);
        return config;
      })
      .catch((res) => {
        return Promise.reject(REFRESH_FAILURE);
      })
    }
    return config;
});

export function userLogin(email, password,callback) {
  const url = `${Config.API_URL}/auth/session`;
  const user_url = `${Config.API_URL}users/me`;
    return (dispatch) => {
        axios.post(url, {'username':email,password})
        .then((res) => {
          if(callback){callback();}
          res.data.AuthenticationResult.expiry = setExpiry();
          dispatch({type:LOGIN_SUCCESS, user:res.data.AuthenticationResult});
          axios.get(user_url, {headers: {'Authorization': `Bearer ${res.data.AuthenticationResult.AccessToken}`}})
          .then((res) => {
            console.log('user attributes success: ',res.data);
            const user_attributes = mapUserAttributes(res.data.UserAttributes);
            return dispatch({
                type: USER_UPDATE_SUCCESS,
                user: user_attributes
            });
          })
          .catch((err) => {
            return dispatch({
              type: USER_UPDATE_FAILURE
            })
          })
        })
        .catch((err)=>{
          return dispatch({
              type: LOGIN_FAILURE,
              err: err.response.data
          })
        });
    }
}

export function userLogout(user,callback){
  console.log('LOGOUt action dispatch');
  const url = `${Config.API_URL}/auth/logout`;
    return (dispatch) => {
        axios.get(url)
        .then((res) => {
          if(callback){callback();}
          return dispatch({
              type: LOGOUT_SUCCESS
          })
        })
        .catch((err)=>{
          if(callback){callback();}
          return dispatch({
              type: LOGOUT_FAILURE
          })
        });

    }
}

export function clearAuthError(){
  return (dispatch) => {
      return dispatch({
        type:CLEAR_AUTH_ERROR
      });
  }
}

function mapUserAttributes(attributes){
  let user_attributes = _.mapKeys(attributes, (value, key) => {
    console.log('value: ',value);
    return value.Name;
  });
  user_attributes = _.mapValues(user_attributes,'Value');
  return user_attributes;
}

const setExpiry = () => {
  const now = new Date();
  return (now.getTime() + (60*50*100));
}



