import { SIGNUP_STEP1 } from '../routes/SignUp/modules/signup.js';

const initialState = {};
export default function currentUserReducer(state = initialState, action) {
  switch (action.type) {
    case SIGNUP_STEP1:
      return {
        loggingIn: true,
        user: action.currentUser
      };
    default:
      return state;
  }
}



