// We only need to import the modules necessary for initial render
import CoreLayout from '../layouts/PageLayout/PageLayout';
import Home from './Home';
import dropletHistory from './User/UserDropletHistory';
import orderSummary from './OrderSummary';
import paymentDetail from './PaymentDetail';
import userProfile from './User/UserProfile';
import userBuyDroplet from './User/UserBuyDroplet';
import userBuyDropletPayment from './User/UserBuyDropletPayment';
import accountPassword from './AccountPassword';
import buyDroplet from './BuyDroplet';
import login from './Login';
import forgotPassword from './ForgotPassword';
import forgotEmail from './ForgotEmail';
import promotions from './User/Promotions';
import userPayment from './User/Payment';
import header from './Header';



export const createRoutes = (store) => ({
    path: '/',
    component: CoreLayout,
    indexRoute: Home(store),
    childRoutes: [
        dropletHistory(store),
        orderSummary(store),
        paymentDetail(store),
        userProfile(store),
        accountPassword(store),
        buyDroplet(store),
        forgotPassword(store),
        forgotEmail(store),
        login(store),
        userBuyDroplet(store),
        promotions(store),
        userPayment(store),
        userBuyDropletPayment(store),
        header(store)
    ]
})

export default createRoutes;



