
export default (store) => ({
    path: 'forgot-email',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            const login = require('./components/ForgotEmailContainer').default
            cb(null, login)

        }, 'forgotEmail')
    }
})



